$bleu-fonce : #154975;
$vert-fonce : rgb(90, 131, 88);
$vert : rgb(94, 194, 97);
$vert-clair : rgb(219, 249, 216);

.navbar {
    z-index: 3;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 80px;
    position: fixed;
    box-sizing: border-box;
    background-color: $vert-clair;

}

.navbar__logo {
    display: flex;
    flex-direction: row;
    width: 100vh;
    min-width: fit-content;
    margin: 20px;

    @media screen and (max-width: 920px) {
        display: none;
    }
}

.navbar__navlink {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 150vh;
    min-width: fit-content;
    gap: 4vh;
    margin: 20px;

    @media screen and (max-width: 920px) {
        justify-content: center;
    }

    @media screen and (max-width: 560px) {
        justify-content: space-around;
    }
}

.navbar__desktop {
    @media screen and (max-width:560px) {
        display: none;
    }
}

.navbar__mobile {
    display: none;

    @media screen and (max-width: 560px) {
        display: flex;
        color: $vert-fonce;
    }
}

.active {
    border-bottom: 2px solid $vert-fonce;

    @media screen and (max-width:560px) {
        border-bottom: 0;
    }
}